<template>
  <el-dialog @close="closeDialog" title="" :visible.sync="groupDialogFormVisible">
    <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="demo-dynamic">
      <el-form-item
        prop="name"
        label=""
        :rules="[
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 5, message: ' 2  5 ', trigger: 'blur' }
        ]"
      >
        <el-input style="width: 70%;" v-model="dynamicValidateForm.name"></el-input>
      </el-form-item>
      <el-form-item
        v-for="(teamate, index) in dynamicValidateForm.teamates"
        :label="' ' + (index+1)"
        :key="teamate.key"
        :prop="'teamates.' + index + '.value'"
        :rules="[
        {required: true, message: '', trigger: 'blur'},
        { min: 2, max: 5, message: ' 2  5 ', trigger: 'blur' }
        ]"
      >
        <el-input style="width: 50%;" v-model="teamate.value"></el-input>
        <el-button style="width: 20%;" @click.prevent="checkteamate()"></el-button>
        <el-button style="width: 20%;" @click.prevent="removeteamate(teamate)"></el-button>
      </el-form-item>
      <el-form-item>
        <el-button @click="addteamate('dynamicValidateForm')"></el-button>
        <br><br>
        <el-button type="primary" @click="open2"></el-button>
        <el-button @click="resetForm('dynamicValidateForm')"></el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>
<script>
export default {
  props: {
    groupDialogFormVisible: {
      type: Boolean,
      default: false
    },
    pid: {
      type: String,
      default: ''
    },
    uid: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      dynamicValidateForm: {
        teamates: [{
          value: ''
        }],
        name: ''
      },
      registerForm: {
        name: ''
      },
      user_pk: '',
      ruleForm: {
        faculty: '',
        name: '',
        gender: '',
        id_card: '',
        student_id: '',
        birth_date: '',
        clothes_size: '',
        cellphone_num: ''
      },
      formLabelWidth: '120px'
    }
  },
  created: function () {
    this.user_info_request(this.uid)
  },
  methods: {
    submitForm (formName) {
      this.$http.post('http://111.230.226.45:8888/api/set_teammate_confirm/' + this.uid + '/' + this.pid)
      this.$emit('finishGroup')
    },
    open2 () {
      this.$msgbox({
        title: '',
        message: '',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            console.log('12312341252345')
            this.submitForm(this.dynamicValidateForm)
            this.closeDialog()
            done()
          } else {
            done()
          }
        }
      }).then(action => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    removeteamate (item) {
      var index = this.dynamicValidateForm.teamates.indexOf(item)
      if (index !== -1) {
        this.dynamicValidateForm.teamates.splice(index, 1)
      }
    },
    addteamate (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.dynamicValidateForm.teamates.push({
            value: '',
            key: Date.now()
          })
        } else {
          console.log('error')
        }
      })
    },
    checkteamate () {
      var i = this.dynamicValidateForm.teamates.length - 1
      console.log(this.dynamicValidateForm.teamates[i].value)
      this.registerForm.name = this.dynamicValidateForm.teamates[i].value
      this.$http.post('http://111.230.226.45:8888/api/add_teammate/' + this.uid + '/' + this.pid, this.registerForm, {emulateJSON: true}).then((response) => {
        var res = JSON.parse(response.bodyText)
        console.log(res)
        if (res.status !== 0) {
          alert('User doesn\'t exsist !')
        } else {
          alert('confirm !')
        }
      })
    },
    user_info_request (username) {
      this.$http.get('http://111.230.226.45:8888/api/user_info_request/' + username).then((response) => {
        var res = JSON.parse(response.bodyText)
        console.log(res)
        if (res.error_num === 0) {
          this.dynamicValidateForm.name = res.list[0].fields.name
          this.ruleForm.birth_date = ''
          this.user_pk = res.list[0].pk
        } else {
          this.$message.error('"')
          console.log(res['msg'])
        }
      })
    },
    closeDialog () {
      this.$emit('finishGroup')
    }
  }
}
</script>
<style scoped>
  .el-button{
    margin: 5px;
  }
</style>



