<template>
  <el-form :model="infoForm" :rules="rules" ref="ruleForm" label-width="80px" :inline="inline">
    <el-form-item label="" prop="degree">
      <el-select v-model="infoForm.reading_degree" placeholder="" class="mid">
        <el-option label="" value="BS"></el-option>
        <el-option label="" value="MS"></el-option>
        <el-option label="" value="PhD"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="" prop="faculty">
      <el-input v-model="infoForm.faculty" placeholder="" class="mid">
      </el-input>
    </el-form-item>
    <br v-if="inline">
    <el-form-item label="" prop="class_id">
      <el-input v-model="infoForm.class_id" class="mid"></el-input>
    </el-form-item>
    <el-form-item label="" prop="name">
      <el-input v-model="infoForm.name" class="mid"></el-input>
    </el-form-item>
    <br v-if="inline">
    <el-form-item label="" prop="gender">
      <el-select v-model="infoForm.gender" placeholder="" class="min">
        <el-option label="" value="M"></el-option>
        <el-option label="" value="F"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="" prop="id_card">
      <el-input v-model="infoForm.id_card" class="mid"></el-input>
    </el-form-item>
    <br v-if="inline">
    <el-form-item label="" prop="student_id">
      <el-input v-model="infoForm.student_id" class="mid"></el-input>
    </el-form-item>
    <el-form-item prop="birth_date" label="">
      <el-date-picker type="date" placeholder="" v-model="infoForm.birth_date" class="mid"></el-date-picker>
    </el-form-item>
    <br v-if="inline">
    <el-form-item label="" prop="clothes_size">
      <el-select v-model="infoForm.clothes_size" placeholder="" class="min">
        <el-option label="XS" value="XS"></el-option>
        <el-option label="S" value="S"></el-option>
        <el-option label="M" value="M"></el-option>
        <el-option label="L" value="L"></el-option>
        <el-option label="XL" value="XL"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="" prop="cellphone_num">
      <el-input v-model="infoForm.cellphone_num" class="mid"></el-input>
    </el-form-item>
    <br v-if="inline">
    <el-form-item label="" prop="email">
      <el-input v-model="infoForm.email" class="mid"></el-input>
    </el-form-item>
    <el-form-item label="" prop="dormitory">
      <el-input v-model="infoForm.dormitory" class="mid"></el-input>
    </el-form-item>
    <br v-if="inline">
    <el-form-item>
      <el-button type="primary" @click="submitForm"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  import ElInput from '../../../node_modules/element-ui/packages/input/src/input.vue'

  export default {
    components: {ElInput},
    data () {
      return {
        id: null,
        infoForm: {
          faculty: '',
          class_id: '',
          reading_degree: '',
          name: '',
          gender: '',
          id_card: '',
          student_id: '',
          birth_date: '',
          clothes_size: '',
          cellphone_num: '',
          email: '',
          dormitory: ''
        },
        rules: {
          name: [
            {
              trigger: 'blur',
              validator: function (rule, value, callback) {
                if (!value || value === '') {
                  callback(new Error(''))
                } else {
                  setTimeout(function () {
                    if (value.length > 10) {
                      callback(new Error('10'))
                    } else callback()
                  }, 1000)
                }
              }
            }
          ],
          student_id: [
            {
              trigger: 'blur',
              validator: function (rule, value, callback) {
                if (value === '') {
                  callback(new Error(''))
                } else {
                  setTimeout(function () {
                    var pattern = /^[0-9]{10}$/g
                    if (!pattern.test(value)) {
                      callback(new Error('10'))
                    } else callback()
                  }, 1000)
                }
              }
            }
          ],
          faculty: [{
            trigger: 'blur',
            validator: function (rule, value, callback) {
              if (value === '') {
                callback(new Error(''))
              } else {
                setTimeout(function () {
                  if (value.length > 20) {
                    callback(new Error('20'))
                  } else callback()
                }, 1000)
              }
            }
          }],
          id_card: [{
            trigger: 'blur',
            validator: function (rule, value, callback) {
              if (value === '') {
                callback(new Error(''))
              } else {
                setTimeout(function () {
                  var pattern = /^[0-9]{18}$/g
                  console.log('here')
                  if (!pattern.test(value)) {
                    callback(new Error('18'))
                  } else callback()
                }, 1000)
              }
            }
          }],
          class_id: [{
            trigger: 'blur',
            validator: function (rule, value, callback) {
              if (value === '') {
                callback(new Error(''))
              } else {
                setTimeout(function () {
//                  var pattern = /^[0-9]{18}$/g
                  if (value.length > 20) {
                    callback(new Error('20'))
                  } else callback()
                }, 1000)
              }
            }
          }],
          reading_degree: [{
            trigger: 'blur',
            validator: function (rule, value, callback) {
              if (value === '') {
                callback(new Error(''))
              } else {
                setTimeout(function () {
                  callback()
                }, 1000)
              }
            }
          }],
          gender: [],
          birth_date: [{
            trigger: 'change',
            validator: function (rule, value, callback) {
              console.log('here', value)
              if (!value || value === '') {
                callback(new Error(''))
              } else {
                setTimeout(function () {
                  if (new Date() < new Date(value)) {
                    callback(new Error(''))
                  } else callback()
                }, 1000)
              }
            }
          }],
          clothes_size: [{
            trigger: 'change',
            validator: function (rule, value, callback) {
              console.log('here', value)
              if (!value || value === '') {
                callback(new Error(''))
              } else {
                setTimeout(function () {
                  callback()
                }, 1000)
              }
            }
          }],
          cellphone_num: [{
            trigger: 'blur',
            validator: function (rule, value, callback) {
              if (value === '') {
                callback(new Error(''))
              } else {
                setTimeout(function () {
                  var pattern = /^[0-9]{11}$/g
                  console.log('here')
                  if (!pattern.test(value)) {
                    callback(new Error('11'))
                  } else callback()
                }, 1000)
              }
            }
          }],
          email: [{
            trigger: 'blur',
            validator: function (rule, value, callback) {
              if (value === '') {
                callback(new Error(''))
              } else {
                setTimeout(function () {
                  var pattern = /^\w+@/g
                  if (!pattern.test(value)) {
                    callback(new Error(''))
                  } else callback()
                }, 1000)
              }
            }
          }],
          dormitory: [{
            trigger: 'blur',
            validator: function (rule, value, callback) {
              console.log('here', value)
              if (!value || value === '') {
                callback(new Error(''))
              } else {
                setTimeout(function () {
                  if (value.length > 20) {
                    callback(new Error('20'))
                  } else callback()
                }, 1000)
              }
            }
          }]

        }
      }
    },
    methods: {
      submitForm () {
//        this.$http({
//          method: 'GET',
//          url:''
//        })
        console.log(this.infoForm)
        this.$http.post(
          'http://111.230.226.45:8888/api/user_info_submit/' + this.id,
          this.infoForm, {emulateJSON: true}
        ).then((response) => {
//          let res = JSON.parse(response.body)
          var res = response
          console.log('response', response)
          if (res.statusText === 'OK') {
            alert('')
          } else {
            alert('')
          }
        })
        this.$emit('submit')
      },
      onSubmit () {
        console.log('submit!')
      },
      storeForm () {
        console.log('store')
        localStorage.setItem('submitform', JSON.stringify(this.form))
      }
    },
    props: {
      inline: Boolean,
      uid: Number
    },
    mounted:
      function () {
        if (this.uid) {
          this.id = this.uid
        } else {
          this.id = this.$route.params.uid
        }
        this.$http.get('http://111.230.226.45:8888/api/user_info_request/' + this.id).then((response) => {
          var res = JSON.parse(response.bodyText)
          console.log(res)
          if (res.error_num === 0) {
//            this.dynamicValidateForm.name = res.list[0].fields.name
//            this.ruleForm.birth_date = ''
            this.infoForm = res.list[0].fields
          } else {
            this.$message.error('"')
            console.log(res['msg'])
          }
        })
      }
  }
</script>

<style scoped>
  .mid {
    width: 220px;
  }

  .min {
    width: 80px;
    margin-right: 140px;
  }
</style>



