<template>
  <el-dialog @close="closeDialog" title="" :visible.sync="dialogFormVisible">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item v-if="this.group === true" label="" prop="teamname">
          <el-input v-model="ruleForm.teamname"></el-input>
        </el-form-item>
        <h4 v-if="this.group === true"></h4>
        <el-form-item label="" prop="faculty">
          <el-select v-model="ruleForm.faculty" placeholder="">
            <el-option label="" value="CS"></el-option>
            <el-option label="" value="AUTO"></el-option>
            <el-option label="" value="EE"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="gender">
          <el-select v-model="ruleForm.gender" placeholder="">
            <el-option label="" value="M"></el-option>
            <el-option label="" value="F"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="id_card">
          <el-input v-model="ruleForm.id_card"></el-input>
        </el-form-item>
        <el-form-item label="" prop="student_id">
          <el-input v-model="ruleForm.student_id"></el-input>
        </el-form-item>
        <el-form-item prop="birth_date">
          <el-date-picker type="date" placeholder="" v-model="ruleForm.birth_date" style="width: 100%;"></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="clothes_size">
          <el-select v-model="ruleForm.clothes_size" placeholder="">
            <el-option label="S" value="S"></el-option>
            <el-option label="M" value="M"></el-option>
            <el-option label="L" value="L"></el-option>
            <el-option label="XL" value="XL"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="cellphone_num">
          <el-input v-model="ruleForm.cellphone_num"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button v-if="group===true" type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button v-else type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
</template>

<script>
export default {
  props: {
    dialogFormVisible: {
      type: Boolean,
      default: false
    },
    pid: {
      type: String,
      default: ''
    },
    uid: {
      type: String,
      default: ''
    },
    group: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      user_pk: '',
      ruleForm: {
        faculty: '',
        name: '',
        gender: '',
        id_card: '',
        student_id: '',
        birth_date: '',
        clothes_size: '',
        cellphone_num: '',
        teamname: ''
      },
      registerForm: {
        user_id: '',
        project_id: ''
      },
      groupForm: {
        group_name: ''
      },
      rules: {
        teamname: [
          { required: true, message: '', trigger: 'blur' }
        ],
        faculty: [
          { required: true, message: '', trigger: 'change' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        gender: [
          { required: true, message: '', trigger: 'change' }
        ],
        id_card: [
          { required: true, message: '', trigger: 'blur' },
          { min: 0, max: 20, message: ' x  x ', trigger: 'blur' }
        ],
        student_id: [
          { required: true, message: '', trigger: 'blur' },
          { legth: 10, message: '10', trigger: 'blur' }
        ],
        clothes_size: [
          { required: true, message: '', trigger: 'change' }
        ],
        cellphone_num: [
          { required: true, message: '', trigger: 'blur' },
          { min: 11, max: 11, message: '12', trigger: 'blur' }
        ]
      },
      formLabelWidth: '120px'
    }
  },
  created: function () {
    this.user_info_request(this.uid)
  },
  methods: {
    user_info_request (uid) {
      console.log(this.group)
      this.$http.get('http://111.230.226.45:8888/api/user_info_request/' + uid).then((response) => {
        var res = JSON.parse(response.bodyText)
        console.log(res)
        if (res.error_num === 0) {
          this.ruleForm = res.list[0].fields
          this.user_pk = res.list[0].pk
          this.ruleForm.birth_date = this.ruleForm.birth_date + 'T00:00:00.000Z'
        } else {
          this.$message.error('"')
          console.log(res['msg'])
        }
      })
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.registerForm.user_id = this.uid
          this.registerForm.project_id = this.pid
          console.log(this.registerForm)
          if (this.group === false) {
            this.$http.post('http://111.230.226.45:8888/api/project_register/' + this.uid + '/' + this.pid).then((response) => {
              var res = JSON.parse(response.bodyText)
              if (res.error_num === 0) {
                alert('')
              }
              if (res.error_num === 1) {
                alert('')
              }
              if (res.error_num === 2) {
                alert('')
              }
              if (res.error_num === 3) {
                alert('')
              }
              if (res.error_num === 4) {
                alert('')
              }
            })
            this.dialogFormVisible = false
            this.closeDialog()
          } else {
            this.groupForm.group_name = this.ruleForm.teamname
            this.$http.post('http://111.230.226.45:8888/api/add_group/' + this.uid + '/' + this.pid, this.groupForm, {emulateJSON: true}).then((response) => {
              this.dialogFormVisible = false
              var res = JSON.parse(response.bodyText)
              if (res.error_num === 0) {
                alert('')
              }
              if (res.error_num === 1) {
                alert('')
              }
              if (res.error_num === 2) {
                alert('')
              }
              if (res.error_num === 3) {
                alert('')
              }
              if (res.error_num === 4) {
                alert('')
              }
              this.$emit('finish')
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    closeDialog () {
      this.$emit('dialogStatus', false)
    }
  }
}
</script>



